import { msdyn365Commerce } from '@msdyn365-commerce/core-internal';
import express, { Request, Response, Router } from 'express';

const getVarianceForUser = (req: Request, res: Response) => {
    const userId = req.body.userId;
    const attributes: { [index: string]: string } = req.body.attributes;
    const activeVariants = msdyn365Commerce.experimentationConnector
        ? msdyn365Commerce.experimentationConnector.provider.getVariantsForUser(userId, attributes)
        : [];

    res.status(200).send(activeVariants);
};

export default function(): Router {
    const experimentationRouter = express.Router();
    experimentationRouter.post('/user-exp-variants', getVarianceForUser);
    return experimentationRouter;
}
